/*  src/Components/Title.css  */
.background {
    width: 100%;
    max-height: 75px;
    top: 0;
    padding: 1em;
    position: absolute;
    background-color: rgb(192, 45, 26);
}

.Title {
    display: flex;
    margin-top: -0.5em;
    justify-content: center;
    color: #fff;
  }

.Title-Subtitle {
    margin-top: -0.5em;
    color: rgba(255, 255, 255, 0.75);
}

.nav-link {
    display: inline-block;
    padding: 0.5em 1em;
    color: rgba(255, 255, 255, 0.835);
    font-weight: bold;
    text-decoration: none;
}

.nav-link-active {
    color: #fff;
    border-bottom: 2px solid;
  }