/* src/Componenets/Home/Home.css */

.container{
    display: block;
    width: 75%;
    margin: auto;
    margin-top: 5.5em;
    color: black;
}

.bold {
    font-weight: 500;
}

.a {
    display: block;
    height: 40vmin;
    width: 40vmin;
    margin: auto;
}

.logo {
    height: inherit;
    pointer-events: none;
}
  
@media (prefers-reduced-motion: no-preference) {
    .logo {
        animation: logo-spin infinite 20s linear;
    }
}

@keyframes logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}